.form-container {
  width: 480px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px;
  margin-top: 24px;
  margin-bottom: 60px;
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
